import Bmob from "hydrogen-js-sdk";

import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"

let that;
let search_text;
let uid;
let shopId;
export default {
	components: {
		uniSegmentedControl,
		faIcon,
		loading
	},
	data() {
		return {
			loading: true,
			staffs: null,
			is_choose: false,
			items: ['已启用', '未启用'],
			current: 0,
			disabled:false
		}
	},

	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid')
		shopId = options.shopId
	},
	onShow() {

		that.getstaff_list()
	},
	onUnload() {
		search_text = ""
	},
	methods: {
		
		//tab点击
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index
		
				if (index == 0) {
					that.disabled = false,
					that.getstaff_list()
				} else if (index == 1) {
					that.disabled = true,
					that.getstaff_list()
				} 
			}
		},

		//选择此员工当负责人
		select_this(charge) {
			uni.setStorageSync("charge", charge)
			uni.navigateBack({
				delta: 1
			})
		},

		//编辑操作
		edit(item) {
			console.log(item)
			uni.setStorageSync("staff", item);
			uni.navigateTo({
				url: "add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此员工',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("staffs");
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				that.getstaff_list()
			}).catch(err => {
				console.log(err)
			})
		},

















		//得到员工列表
		getstaff_list: function() {
			const query = Bmob.Query("staffs");
			query.order("-createdAt");
			query.equalTo("masterId", "==", uid);
			query.equalTo("shop", "==", shopId);
			query.equalTo("disabled", "==", that.disabled);
			if (search_text) {
				query.equalTo("username", "==", {
					"$regex": "" + search_text + ".*"
				});

			}
			query.find().then(res => {
				//console.log(res)
				that.loading = false;
				that.staffs = res;
			});
		},

	}
}
